.about {
    display: flex;
    align-items: center;
    padding: 60px 0px;
    padding-bottom: 0px;
    background-color: $primary_b-c;;
}

.about_title {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 32px;
    @media screen and (min-width: 768px) 
    {
        margin-bottom: 72px;
}
@media screen and (min-width: 1200px) 
{
    margin-bottom: 72px;
}
}

.about_title--name {
    font-style: normal;
    font-weight: 400;
    font-size: 30px;
    line-height: calc(38/30);
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $text-wh; 
    @media screen and (min-width: 768px) 
    {
        font-size:  34px; 
}
@media screen and (min-width: 1200px) 
{
    font-size: 36px;
}
}

.about_title--description {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    text-align: center;
    text-transform: uppercase;
    color: $text-wh;

@media screen and (min-width: 768px) 
    {
        margin-bottom: 12px;
        font-size: 22px;
}
    
    @media screen and (min-width: 1200px) 
{
    font-size: 30px;
}}

.about_container {
    @media screen and (min-width: 768px) 
    {
        display: flex;
        flex-direction:row;
    }   
    }

.about_container--box {
    @media screen and (min-width: 768px) 
    {
      width: 50%;
      align-items: flex-start;
      padding: 12px 12px 0px;
    }
    @media screen and (min-width: 1200px) 
    {
      padding-right: 100px;
      padding-top: 100px;
    }
}

.about_image--block {
    position: relative;
    width: 278px;
    height: 278px;
    margin: 0px auto 62px;
    border-radius: 50%;
    background-color: rgba(248, 210, 78, 0.7);

    @media screen and (min-width: 768px) 
    {
      width: 330px;
      height: 330px;
      margin-bottom: 0px;
      margin-right: 20px;
    }
    @media screen and (min-width: 1200px) 
{
    width: 500px;
    height: 500px;
    margin-left: 0px;
    margin-right: 88px;
}
}

.about_image {
    position: absolute;
    top: 22px;
    width: 278px;
    height: 278px;
   ;

    @media screen and (min-width: 768px) 
    {
      width: 330px;
      height: 330px;
    }
    @media screen and (min-width: 1200px) 
    {
        width: 500px;
        height: 500px;
        top: 39px;
    }
}

.about_text {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 176%;
    letter-spacing: 0.04em;
    color: $text-wh;
    margin-bottom: 4px;
    padding: 0px 24px;
    text-align: center;

    @media screen and (min-width: 768px) 
    {
      text-align: start;
      margin-bottom: 8px;
      padding: 0px;
    }
    @media screen and (min-width: 1200px) 
    {
      font-size: 14px;
    }   
}
.about_text:first-child {
    @media screen and (min-width: 1200px) 
    {
        font-size: 16px; 
    }
}


.text-hidden {
    @media screen and (max-width: 767px) 
    {
    display: none;
}
}

.btn--readmore {
    margin: 20px auto;

    @media screen and (min-width: 768px) 
    {
        margin: 48px 0px 0px 0px;

}
}

.about_card {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-bottom: 28px;
}

.about_icon-area {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-bottom: 16px;
    background-color: $logo;
}

.about_card--value {
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.17;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $text-wh;
    margin-bottom: 18px;
}

.about_card--text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 170%;
    text-align: center;
    color: $text-wh;
    padding: 0px 20px;
}